import { Icon } from '@chakra-ui/icons';
import { Box, HStack, Input, InputGroup, InputRightElement, Skeleton, Text, VStack } from '@chakra-ui/react';
import { useCallback, useEffect, useRef, useState } from 'react';
import { Motion, spring } from 'react-motion';

import useSendMessage from './hooks/useSendMessage';
import ReceivingMessage from './ReceivingMessage';
import useCreateChatSession from './hooks/useCreateChatSession';
import useFetchActiveChatSession from './hooks/useFetchActiveChatSession';
import useCreateChatLogs from './hooks/useCreateChatLogs';
import useEndChatSession from './hooks/useEndChatSession';
import useFetchTodayChatLogs from './hooks/useFetchTodayChatLogs';

import s from './global.module.css';

type AppProps = {
  useCaseId: string;
  welcomeMessage: string;
  botName: string;
};

const presets = {
  default: { stiffness: 330, damping: 20 },
};

/**
 * <Button />
 */
const Button = ({ active, onClick }: { active: any; onClick: any }) => {
  return (
    <Motion
      defaultStyle={{
        opacity: 0,
        scale: 0,
      }}
      style={{
        opacity: spring(1, presets.default),
        scale: spring(1, presets.default),
      }}
    >
      {(interpolatingStyles) => (
        <button
          className={s.wentilabs_chat_button}
          onClick={onClick}
          style={{
            opacity: interpolatingStyles.opacity,
            transform: `scale(${interpolatingStyles.scale})`,
          }}
        >
          <LaunchIcon active={active} />
          <CloseIcon active={active} />
        </button>
      )}
    </Motion>
  );
};

/**
 * <CloseIcon />
 */
const CloseIcon = ({ active }: { active: any }) => (
  <Motion
    defaultStyle={{
      opacity: 0,
      rotate: -45,
      scale: 0,
    }}
    style={{
      opacity: spring(active ? 1 : 0, presets.default),
      rotate: spring(active ? 0 : -45, presets.default),
      scale: spring(active ? 1 : 0, presets.default),
    }}
  >
    {(interpolatingStyles) => (
      <img
        alt="Close Icon"
        className={s.wentilabs_chat_button_chat_icon__close}
        src="https://js.intercomcdn.com/images/launcher-close-icon@2x.d4b89535.png"
        style={{
          opacity: interpolatingStyles.opacity,
          transform: `
            rotate(${interpolatingStyles.rotate}deg) 
            scale(${interpolatingStyles.scale})
            translate(-50%, -50%)
          `,
        }}
      />
    )}
  </Motion>
);

/**
 * <LaunchIcon />
 */
const LaunchIcon = ({ active }: { active: any }) => (
  <Motion
    defaultStyle={{
      opacity: 1,
      rotate: 0,
      scale: 1,
    }}
    style={{
      opacity: spring(active ? 0 : 1, presets.default),
      rotate: spring(active ? 45 : 0, presets.default),
      scale: spring(active ? 0 : 1, presets.default),
    }}
  >
    {(interpolatingStyles) => (
      <img
        alt="Launcher Icon"
        className={s.wentilabs_chat_button_chat_icon__launcher}
        src="https://app.wentilabs.com/logos/aceturtle-chat-widget.png"
        style={{
          opacity: interpolatingStyles.opacity,
          transform: `
            rotate(${interpolatingStyles.rotate}deg) 
            scale(${interpolatingStyles.scale})
            translate(-50%, -50%)
          `,
        }}
      />
    )}
  </Motion>
);

export default function App({ useCaseId, welcomeMessage, botName }: AppProps) {
  const chatInputRef = useRef<HTMLInputElement>(null);
  const messagesRef = useRef<HTMLDivElement>(null);

  const [messages, setMessages] = useState<any>([
    { id: 1, content: welcomeMessage, contentProductLink: null, contentImageLink: null, from: 'assistant' },
  ]);
  const [templateMessages] = useState<string[]>([
    'Recommend me toys for a 4 years old girl who likes princess',
    'Recommend me any toys for a 8 years old boy',
  ]);
  const [inputValue, setInputValue] = useState('');

  const { mutate: createChatSession, data: createChatSessionData } = useCreateChatSession();
  const { mutate: createChatLog } = useCreateChatLogs();
  const { mutate: endSession } = useEndChatSession({ useCaseId });

  const currentActiveChatSession = useFetchActiveChatSession({ useCaseId });
  const myTodayChatLogs = useFetchTodayChatLogs();

  const activeChatSessionId = currentActiveChatSession?.data?.id || createChatSessionData?.data?.[0]?.id;
  const activeChatSessionCreatedDate =
    currentActiveChatSession?.data?.created_at || createChatSessionData?.data?.[0]?.created_at;

  const {
    nextMessageId,
    mutate: sendMessage,
    data: sendMessageRes,
    isLoading: receivingRes,
    reset: resetSendMessage,
  } = useSendMessage({ useCaseId, chatSessionId: activeChatSessionId || '' });

  const disableInput = receivingRes || !inputValue;
  const detectLinkOrImage = sendMessageRes.link !== null || sendMessageRes.image !== null;
  const showMessageTemplate = messages.length === 1;

  // end session if now is new day
  useEffect(() => {
    if (activeChatSessionCreatedDate) {
      const currentDate = new Date().toISOString().split('T')[0];
      const parseActiveChatSessionCreatedDate = activeChatSessionCreatedDate.split('T')?.[0];

      if (currentDate !== parseActiveChatSessionCreatedDate) {
        endSession?.();
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [activeChatSessionCreatedDate]);

  useEffect(() => {
    if (myTodayChatLogs.data?.length && messages.length === 1) {
      setMessages([
        ...messages,
        ...myTodayChatLogs.data.map((chatLog: any, idx: number) => {
          try {
            const parseContent = JSON.parse(chatLog.full_message);

            return {
              id: idx + 2,
              content: parseContent.content,
              contentProductLink: parseContent.contentProductLink,
              contentImageLink: parseContent.contentImageLink,
              from: chatLog.from,
            };
          } catch (err) {
            return {
              id: idx + 2,
              content: chatLog.message || '',
              contentProductLink: null,
              contentImageLink: null,
              from: chatLog.from,
            };
          }
        }),
      ]);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [myTodayChatLogs.data?.length]);

  // after receive message
  useEffect(() => {
    if (sendMessageRes.text !== null) {
      const currentMessageesLength = messages.length;
      const newId = currentMessageesLength + 1;

      if (newId > nextMessageId) {
        const newMessages = [...messages];
        newMessages[newMessages.length - 1] = {
          ...newMessages[newMessages.length - 1],
          content: sendMessageRes.text,
          contentProductLink: sendMessageRes.link,
          contentImageLink: sendMessageRes.image,
        };
        setMessages(newMessages);
      } else {
        const newMessage = {
          id: newId,
          content: sendMessageRes.text,
          contentProductLink: sendMessageRes.link,
          contentImageLink: sendMessageRes.image,
          from: 'assistant',
        };

        setMessages([...messages, newMessage]);
      }

      if (!receivingRes) {
        const newChatLog = {
          message: sendMessageRes.text,
          full_message: JSON.stringify({
            content: sendMessageRes.text,
            contentProductLink: sendMessageRes.link,
            contentImageLink: sendMessageRes.image,
          }),
          usecase_id: useCaseId,
          from: 'assistant',
          chat_session_id: activeChatSessionId,
        };

        createChatLog(newChatLog);
        resetSendMessage();

        chatInputRef && chatInputRef.current?.focus();
        setTimeout(() => {
          messagesRef?.current?.scroll({ top: messagesRef.current.scrollHeight, behavior: 'smooth' });
        }, 1000);
      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [sendMessageRes, receivingRes]);

  // create new chat session if no current chat session is active
  useEffect(() => {
    if (currentActiveChatSession?.data === null) {
      createChatSession(useCaseId);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentActiveChatSession?.data]);

  const handleSendMessage = (message?: string) => {
    if (disableInput && !message) {
      return;
    }

    const value = message || inputValue;

    const newChatLog = {
      message: value,
      full_message: JSON.stringify({ content: value }),
      usecase_id: Number(useCaseId),
      from: 'user',
      chat_session_id: activeChatSessionId,
    };
    createChatLog(newChatLog);

    const newId = messages.length + 1;

    sendMessage(value, newId + 1);

    setMessages([...messages, { id: newId, content: value, from: 'user' }]);
    setInputValue('');

    setTimeout(() => {
      messagesRef?.current?.scroll({ top: messagesRef.current.scrollHeight, behavior: 'smooth' });
    }, 500);
  };

  const [active, setActive] = useState(true);
  const [height, setHeight] = useState(670);
  const [stable, setStable] = useState(false);

  useEffect(() => {
    if (window) {
      const height = window.innerHeight;
      setHeight(height - height * 0.2);
    }

    return () => {
      setStable(false);
    };
  }, []);

  const handleRest = () => {
    setStable(true);
  };

  const handleClick = useCallback(() => {
    setActive(!active);
  }, [active]);

  const generateProductLink = (link: string) => {
    const utmSourcePath = 'utm_source=ai-chat&utm_medium=wenti&utm_campaign=Geoffrey-Toy-Assistan';
    const sign = link?.includes('?') ? '&' : '?';

    return `${link}${sign}${utmSourcePath}`;
  };

  return (
    <Box className={s.wentilabs_chat}>
      {active && (
        <Motion
          defaultStyle={{
            y: 20,
            opacity: 0,
          }}
          style={{
            y: spring(active ? 0 : 20, presets.default),
            opacity: spring(active ? 1 : 0, presets.default),
          }}
          onRest={handleRest}
        >
          {(interpolatingStyles) => (
            <div
              className={s.wentilabs_chat_base}
              style={{
                height,
                opacity: interpolatingStyles.opacity,
                transform: `translateY(${interpolatingStyles.y}px)`,
              }}
            >
              <Motion
                defaultStyle={{
                  y: 60,
                }}
                style={{
                  y: spring(active && stable ? 60 : 60, presets.default),
                }}
              >
                {() => <div className={s.wentilabs_chat_wentilabs_chat_header}>{botName}</div>}
              </Motion>

              <Box className={s.wentilabs_chat_content } ref={messagesRef}>
                {messages.map((message: any) => {
                  if (message.from === 'user') {
                    return (
                      <VStack key={message.id} className={s.wentilabs_chat_message_wrapper}>
                        <Box className={s.wentilabs_chat_message_wrapper_chat_message_me}>{message.content}</Box>
                      </VStack>
                    );
                  }

                  return (
                    <VStack key={message.id} className={s.wentilabs_chat_message_wrapper_bot}>
                      <Box className={s.wentilabs_chat_message_wrapper_bot_chat_message_bot}>{message.content}</Box>

                      {!!detectLinkOrImage &&
                        (message.contentProductLink === '' || message.contentImageLink === '') &&
                        receivingRes && <Skeleton borderRadius="15px" w="250px" h="300px" />}

                      {message.contentProductLink && message.contentImageLink && (
                        <Box className={s.wentilabs_chat_message_wrapper_bot_chat_message_link_wrapper}>
                          <img
                            src={message.contentImageLink}
                            alt="Product"
                            width="580"
                            height="580"
                            onClick={() => {
                              window.open(
                                generateProductLink(message.contentProductLink),
                                '_blank',
                                'noopener,noreferrer',
                              );
                            }}
                            style={{ cursor: 'pointer', borderRadius: '6px' }}
                          />

                          <a
                            className={s.wentilabs_chat_message_wrapper_bot_chat_message_link_wrapper_chat_message_product_link}
                            target="_blank"
                            rel="noopener noreferrer"
                            href={generateProductLink(message.contentProductLink)}
                          >
                            Click to see details
                          </a>
                        </Box>
                      )}
                    </VStack>
                  );
                })}

                {receivingRes && !sendMessageRes.text && <ReceivingMessage />}
              </Box>

              <Box className={s.wentilabs_chat_footer}>
                {showMessageTemplate && (
                  <HStack className={s.wentilabs_chat_footer_message_template_wrapper}>
                    {templateMessages.map((templateMsg: string, idx: number) => (
                      <Box
                        w="49%"
                        key={idx}
                        className={s.wentilabs_chat_footer_message_template_wrapper_message_template_item}
                        onClick={() => handleSendMessage(templateMsg)}
                      >
                        {templateMsg}
                      </Box>
                    ))}
                  </HStack>
                )}

                <InputGroup className={s.wentilabs_chat_footer_chat_input_group}>
                  <Input
                    className={s.wentilabs_chat_footer_chat_input_group_chat_input}
                    ref={chatInputRef}
                    disabled={receivingRes}
                    placeholder="Ask me anything"
                    value={inputValue}
                    onChange={(event: React.ChangeEvent<HTMLInputElement>) => setInputValue(event.target.value)}
                    onKeyDown={(event: React.KeyboardEvent<HTMLInputElement>) => {
                      if (event.key === 'Enter') {
                        handleSendMessage();
                      }
                    }}
                    _focusVisible={{ border: 'none' }}
                  />

                  <InputRightElement className={s.wentilabs_chat_footer_chat_input_group_chat_input_right_element}>
                    <Icon
                      width="23"
                      height="22"
                      viewBox="0 0 23 22"
                      fill="none"
                      xmlns="http://www.w3.org/2000/svg"
                      onClick={() => handleSendMessage()}
                    >
                      <path
                        fillRule="evenodd"
                        clipRule="evenodd"
                        d="M21.9507 0.0813891C22.1899 -0.0439153 22.4787 -0.0228718 22.6987 0.133041C22.9187 0.289911 23.0316 0.555824 22.9924 0.82365L20.3619 18.5193C20.3304 18.7288 20.207 18.9153 20.0252 19.0263C19.9114 19.0951 19.7823 19.1305 19.6522 19.1305C19.5738 19.1305 19.4953 19.1171 19.4207 19.0923L9.3935 15.665L19.2533 3.786L6.5115 14.6808L0.528456 12.6357C0.257761 12.543 0.0674126 12.2981 0.0454126 12.0111C0.0243691 11.7251 0.174543 11.4535 0.428978 11.3205L21.9507 0.0813891ZM8.41302 21.2826V16.8463L12.3032 18.1758L9.70815 21.7073C9.57042 21.8948 9.35424 22 9.13042 22C9.05581 22 8.98024 21.9885 8.90659 21.9646C8.61198 21.8671 8.41302 21.5925 8.41302 21.2826Z"
                        fill={disableInput ? '#D7D9DE' : '#72BE44'}
                      />
                    </Icon>
                  </InputRightElement>
                </InputGroup>

                <Text className={s.wentilabs_chat_footer_chat_disclamer}>
                  Geoffrey is an experiment with Generative AI and it may display inaccurate info.
                </Text>

                <Text className={s.wentilabs_chat_footer_chat_powered_by}>
                  Powered by{' '}
                  <a href="https://wentilabs.com" rel="noopener noreferrer" target="_blank" className={s.wentilabs_chat_footer_chat_powered_by_link}>
                    Wenti Labs
                  </a>
                </Text>
              </Box>
            </div>
          )}
        </Motion>
      )}

      <Button active={active} onClick={handleClick} />
    </Box>
  );
}
