import { useQuery } from '@tanstack/react-query';

import useGenerateBrowserUID from './useGenerateBrowserUID';
import useSupabaseClient from './useSupabaseClient';

export default function useFetchTodayChatLogs() {
  const deviceUUID = useGenerateBrowserUID();
  const { data: supabase }: any = useSupabaseClient();

  return useQuery({
    queryKey: ['useFetchTodayChatLogs', deviceUUID],
    queryFn: async () => {
      const currentDate = new Date().toISOString().split('T')[0];

      const { data, error } = await supabase
        .from('chat_logs')
        .select()
        .eq('session_id', deviceUUID)
        .filter('created_at', 'gte', currentDate);

      if (error) {
        return [];
      }

      return data;
    },
    enabled: !!supabase && !!deviceUUID,
  });
}
